import React from 'react';
import './Tela5.css';
import {Link} from 'react-router-dom';


function Tela5(){
    return(
        <section>
            <div>
            <img className="texto3" alt="imagem" src = {require('../imagens/texto3.png')}/>
            </div>

            <div >
            <Link to="/Tela6">
                <img className="continuar" alt="imagem" src = {require('../imagens/continuar.png')}/>
            </Link>

            </div>
        </section>



    );
}

export default Tela5;