import React from 'react';
import './Tela4.css';
import {Link} from 'react-router-dom';


function Tela4(){
    return(
        <section>
            <div>
            <img className="texto2" alt="imagem" src = {require('../imagens/texto2.png')}/>
            </div>

            <div >
            <Link to="/Tela6">
                <img className="botao2" alt="imagem" src = {require('../imagens/botao1.png')}/>
            </Link>

            <Link to="/Tela5">
                <img className="botao3" alt="imagem" src = {require('../imagens/botao2.png')}/>
            </Link>

            </div>
        </section>



    );
}

export default Tela4;