import React, {Component} from 'react';
import './global.css';
import{BrowserRouter, Switch, Route} from 'react-router-dom';
import Home from './component/Home';
import Tela from './component/Tela';
import Tela1 from './component/Tela1';
import Tela2 from './component/Tela2';
import Tela3 from './component/Tela3';
import Tela4 from './component/Tela4';
import Tela5 from './component/Tela5';
import Tela6 from './component/Tela6';
import PFase1 from './component/PFase1';
import PFase2 from './component/PFase2';
import PFase3 from './component/PFase3';
import PFase4 from './component/PFase4';
import PFase5 from './component/PFase5';
import PFase6 from './component/PFase6';
import PFase7 from './component/PFase7';


class App extends Component{
    render(){
        return(
            <BrowserRouter>
                <Switch>
                <Route exact path= "/" component={Home}/>
                <Route exact path= "/Tela" component={Tela}/>
                <Route exact path= "/Tela1" component={Tela1}/>
                <Route exact path= "/Tela2" component={Tela2}/>
                <Route exact path= "/Tela3" component={Tela3}/>
                <Route exact path= "/Tela4" component={Tela4}/>
                <Route exact path= "/Tela5" component={Tela5}/>
                <Route exact path= "/Tela6" component={Tela6}/>
                <Route exact path= "/PFase1" component={PFase1}/>
                <Route exact path= "/PFase2" component={PFase2}/>
                <Route exact path= "/PFase3" component={PFase3}/>
                <Route exact path= "/PFase4" component={PFase4}/>
                <Route exact path= "/PFase5" component={PFase5}/>
                <Route exact path= "/PFase6" component={PFase6}/>
                <Route exact path= "/PFase7" component={PFase7}/>
                </Switch>
            </BrowserRouter>

        );

    }
}

export default App