import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './Home.css';

class Home extends Component{
    render(){
        return(
               <div className="botao">
                    <Link to="/Tela">
                    <img className="continuar" alt="imagem" src = {require('../imagens/continuar.png')}/>
                        </Link>
                 </div>
                
        );
    }
}

export default Home;
