import React from 'react';
import './Tela6.css';
import {Link} from 'react-router-dom';


function Tela6(){
    return(
        <section>
            <div>
            <img className="texto4" alt="imagem" src = {require('../imagens/texto4.png')}/>
            </div>

            <div >
            <Link to="/Nivel1">
                <img className="continuar1" alt="imagem" src = {require('../imagens/continuar.png')}/>
            </Link>

            </div>
        </section>



    );
}

export default Tela6;