import React from 'react';
import './Tela.css';
import Board from './Board';
import Card from './Card';
import {Link} from 'react-router-dom';


function Tela(){
    return(
        <section>
            <main className="flexbox">
               <div className="img"></div>


            <Card id="card-1" className="card" draggable="true">
            </Card>

            <Card id="card-2" className="card1" draggable="true">
            </Card>

            <Card id="card-3" className="card2" draggable="true">
            </Card>
            
            <Card id="card-4" className="card3" draggable="true">
            </Card>
               
            <Board className="board">
            </Board>
            </main>

            <div className="botao">
            <Link to="/Tela1">Continuar</Link>
            </div>
        </section>

    );
}

export default Tela;